<template>
    <div>
        <head-top webActive="home"></head-top>
        <div id="home">
            <div class="sortBox" @mouseleave="activeHidden">
                <div class="container sortInbox">
                    <div :class="active==index&&activeShow?'nor act':'nor'" v-for="(item,index) in sort" :key="index" @mouseenter="choice(index)">{{sort[index].name}}</div>
                </div>
                <div class="childSort">
                    <div class="container">
                        <div class="cld_box" v-for="(item,index) in sort[active].children" :key="index" @click="toList(item.id,item.name)">
                            <img class="cld_img" :src="'http://www.huaxuejia.cn/Public/'+item.pic">
                            <span>{{item.name}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="container">
                <div class="topTit" v-if="titleName">- {{titleName}} -</div>
                <div class="refresh" v-if="titleName" @click="initData()">点击恢复显示所有类别<i class="el-icon-refresh-right ml5"></i></div>
                <div class="homeBox flex f-wrap">
                    <div class="tc w" v-if="product.length==0">
                        <img src="../../images/emptyCart.png" height="200" width="200" class="mt30"/>
                        <p class="f16 tc mt30">暂无该类别相关数据</p>
                    </div>
                    <div v-else class="proBox" v-for="(item,index) in product" :key="index">
                        <div class="proFloat">
                            <div class="pImg" @click="toDetail(item.goods_id,item.specifications_id)">
                                <el-image class="homeProImg" :src="item.showPicIndex?item.upphotos[item.showPicIndex]:item.upphotos[0]" fit="contain"></el-image>
                            </div>
                            <div class="proInfo">
                                <div class="gallery flex j-bet"  v-if="item.upphotos.length!=1">
                                    <div class="galleryPic flex j-bet">
                                        <div class="imgStandard miniPic" @mouseover="selectPic(index,cindex)" @mouseout="outPic(index,cindex)" v-for="(iitem,cindex) in item.upphotos.slice(0,4)" :key="cindex">
                                            <a href="javascript:void(0);">
                                                <img :src="iitem">
                                            </a>
                                        </div>
                                    </div>
                                </div>
                                <div class="proMess flex j-bet">
                                    <div class="proSize f12" v-if="item.cas">CAS：{{item.cas}}</div>
                                    <div class="proSize f12" v-else>无CAS号</div>
                                    <div class="proControl flex j-bet">
                                        <i @click="toCollect(index)" :class="item.isCollect?'el-icon-star-on proIcon active':'el-icon-star-off proIcon'"></i>
                                        <i class="el-icon-shopping-cart-1 proIcon" @click="addCart(item.goods_id,item.specifications_id)"></i>
                                    </div>
                                </div>
                                <div class="proTit">
                                    <div class="proName wto" @click="toDetail(item.goods_id,item.specifications_id)">{{item.goods_name}}</div>
                                    <div class="proBuy flex j-bet tc">
                                        <div class="colorPrice price w">{{item.unit_price}}<span class="f14">/{{item.supply_unit}}</span></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {home,addToCart,addCollection,delMyCollection,category,categoryGoods} from '../../service/api';
    import {getStore} from '../../config/mUtils';
    import headTop from '../../components/header/headTop';
    import {mapActions} from 'vuex'
    // homeProduct
    export default {
        name: 'home',
        data(){
            return{
                product:[],
                sort:[{children:{}}],
                active:0,
                activeShow:false,
                categoryId:'',
                titleName:'',
            }
        },
        components:{
            headTop
        },
        mounted(){
            // 获取首页产品
            this.initData();
        },
        computed:{
        },
        methods:{
            ...mapActions([
                'initCart'
            ]),
            async initData(){
                this.product = await home();
                for(var i = 0 ; i < this.product.length ; i++){
                    this.$set(this.product[i], 'showPicIndex', 0);
                }
                this.categoryId ='';
                this.titleName ='';
                
                var res = await category();
                this.sort = res;
            },
            selectPic(index,cindex){
                var _this =this;
                //_this.product[index].showPicIndex = cindex;
                _this.$set(_this.product[index], 'showPicIndex', cindex);
            },
            outPic(index,cindex){
                var _this =this;
                //_this.$set(_this.product[index], 'showPicIndex', 0);
            },
            showGoodDetail(item){
                var _this = this;
                //this.SAVE_ORDER(item);
                //this.preventRepeat = false;
                //_this.$router.push('/good/detail');
                //console.log(item);
            },
            async addCart(goods_id,specifications_id){
                let res = await addToCart(goods_id, specifications_id, 1);
                if(res){
                    this.$message({
                        message: res.msg,
                        type: res.status==12000?'success':'warning'
                    });
                    this.initCart();
                }
            },
            async toCollect(index){
                if(this.product[index].isCollect){
                    //已经收藏过
                    this.$message({
                        message: '您已收藏过该产品',
                        type: 'info'
                    });
                    // this.$confirm('你确定要取消该收藏吗?', '', {
                    //     confirmButtonText: '确定',
                    //     cancelButtonText: '取消',
                    //     type: 'warning'
                    // }).then(async ()=> {
                    //     let res = await delMyCollection(this.product[index].collectId);
                    //     if(res.status == 12000){
                    //         this.$message({
                    //             message:res.msg,
                    //             type:'success'
                    //         });
                    //         this.initData();
                    //
                    //     }else{
                    //         this.$message({
                    //             message:res.msg,
                    //             type:'warning'
                    //         });
                    //     }
                    // }).catch(() => {
                    //
                    // });

                }else{
                    console.log("index:"+index);
                    //goods_id,specifications_id,rows,page
                    console.log(this.product[index].goods_id);
                    console.log(this.product[index].specifications_id);

                    var resAdd = await addCollection(
                        this.product[index].goods_id,
                        this.product[index].specifications_id
                    );
                    if(resAdd){
                        this.$message({
                            message: resAdd.msg,
                            type: resAdd.status==12000?'success':'warning'
                        });
                        this.initData();
                    }
                }


            },
            toDetail(goods_id,specifications_id){
                this.$router.push({path:"/good/detail",query:{goods_id:goods_id,specifications_id:specifications_id}});
            },
            choice(index){
                this.active = index;
                this.activeShow =true;
            },
            async toList(categoryId,titleName){
                this.categoryId = categoryId;
                this.titleName = titleName;
                var res = await categoryGoods(this.categoryId);
                    this.product = res;
                //this.$router.push({path:"/home/home",query:{categoryId:categoryId,titleName:titleName}});
            },
            activeHidden(){
                this.activeShow = false;
            }

        },
        created(){


        },
    }

</script>

<style scoped>
    #home{
        background-color:#f2f2f2;
        padding-bottom:10px;
        min-height: 760px;
    }
    .homeBox{
        padding:15px 0;
    }
    .sortBox{
        background-color: #fff;
        border-top:1px solid #ddd;
        border-bottom:1px solid #ddd;
        height: 36px;
        line-height: 36px;
        position: relative;
        cursor: default;
        font-size: 14px;
    }
    .nor{
        height: 35px;
        line-height: 35px;
        padding:0 10px; 
    }
    .act{
        color:#0bb8b2;
        border-bottom: 2px solid #0bb8b2;
    }
    .childSort{
        visibility: hidden;
        position: absolute;
        top:37px;
        left:0;
        display: flex;
        flex-wrap: nowrap;
        width:100%;
        z-index: 100000;
        background-color: #fff;
        border-bottom: 1px solid #ddd;
    }
    .sortBox:hover .childSort{
        visibility:visible;
    }
    .cld_box{
        width:160px;
        height:90px;
        display: inline-block;
        text-align: center;
        padding-top:10px;
    }
    .cld_box:hover{
        box-shadow: 0 0 5px 5px #ddd;
        cursor: pointer;
    }
    .cld_box span{
        width: 100%;
        display: block;
        height: 24px;
        line-height: 24px;
    }
    .cld_img{
        width:50px;
        height:50px;
        display: block;
        margin:0 auto;
    }
    .sortInbox{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
    }
    .topTit{
        text-align: center;
        font-size: 24px;
        margin-top:20px;
    }
    .refresh{
        color:#0bb8b2;
        font-size: 13px;
        text-align: center;
        margin-top:5px;
        cursor: pointer;
    }
    .proBox{
        background-color:#fff;
        border:1px solid #f0f0f0;
        padding:15px;
        width:194px;
        position: relative;
        max-width:194px;
        min-height:280px;
        margin-bottom:15px;
        margin-right:15px;
        position: relative;
    }
    .proFloat{
        width:100%;
        height:auto;
        position:absolute;
        top:0;
        left: 0;
        overflow: hidden;
    }
    .proFloat:hover{
        z-index: 1000;
    }
    .proBox:nth-child(5n){
        margin-right:0;
    }
    .proFloat:hover{
        box-shadow:0 3px 10px 0 rgba(0,0,0,.2);
    }
    .proBox:hover .gallery{
        display:flex;
    }
    /*.pImg{*/
    /*    width:100%;*/
    /*    height:190px;*/
    /*    line-height: 188px;*/
    /*    margin-bottom:5px;*/
    /*}*/

    /*.pImg a img{*/
    /*    max-width:170px;*/
    /*    max-height:180px;*/
    /*    background-color: #f4f4f4;*/
    /*}*/
    .proInfo{
        z-index: 10;
        background-color:#fff;
        width:194px;
        padding:0 15px 15px 15px;
    }
    .proMess{
        border-bottom:1px solid #e4e4e4;
        padding-bottom:5px;
        margin-bottom:5px;
    }
    .proName{
        color:#333;
        margin-bottom:5px;
        font-size:14px;
    }
    .price{
        font-size: 18px;
    }
    .proSize{
        color:#0bb8b2;
    }
    .proIcon{
        font-size: 20px;
        color:#999;
        padding:0px 0 0 8px;
    }
    .proIcon:hover{
        color:#0bb8b2;
    }
    .proIcon.active{
        color:#0bb8b2;
    }
    .gallery{
        margin-top:5px;
        display: none;
    }
    .galleryPic{
        flex-wrap:no-wrap;
        overflow:hidden;
    }
    .miniPic{
        width:42px;
        height:42px;
        line-height: 40px;
        margin:0 5px 5px 0;
        border:1px solid #eee;
    }
    .miniPic a img{
        max-width:42px;
        max-height:42px;
    }
    .miniPic:hover{
        border:1px solid #0bb8b2;
    }
    /*.arrow{*/
    /*    width:20px;*/
    /*    line-height: 20px;*/
    /*    background-color:#f0f0f0;*/
    /*    border:1px solid #f0f0f0;*/
    /*    height:42px;*/
    /*    text-align: center;*/
    /*    vertical-align: middle;*/
    /*    line-height: 42px;*/
    /*    cursor: pointer;*/
    /*}*/
    .bigPrice{
        width:100%;
        border:1px solid #0bb8b2;
        font-size: 12px;
        margin-top:10px;
    }
    .bigPrice_tit{
        background-color: #0bb8b2;
        color:#fff;
        padding:0 5px;
    }
    .bigPrice_info{
        padding:0 5px;
        color:#0bb8b2;
    }
    .pImg{
        width:100%;
        height:180px;
        line-height: 180px;
        margin-bottom:5px;
        text-align: center;
        padding:10px 0;
        overflow: hidden;
    }
    .homeProImg{
        width:180px;
        height:180px;
        line-height: 400px;
        object-fit: contain;
    }
    .pImg:hover .homeProImg{
        width:210px;
        height: 210px;
        transition: all .1s ease 0s;
    }


</style>



